$reset-mark-background: #ff0;
$reset-color-mark: #000;
$reset-font-monospace: monospace, monospace;
$reset-color-fieldset-border: #c0c0c0;

html {
  font-family: $reset-font-sans-serif; /* 1 */
  -ms-text-size-adjust: 100%; /* 2 */
  -webkit-text-size-adjust: 100%; /* 2 */
}

